@import url(../fontawesome/fontawesome/css/all.css);
:root {
  --font-size-initial: 12px;
  --font-size-initial-lv2: 14px;
  --font-size-initial-lv3: 16px;
  --font-size-initial-lv4: 18px;
  --spacing-site: 2em;
  --color-text-main: #000;
  --color-text-header: rgb(72, 191, 22);
  --color-text-hoover: /*rgb(44, 76, 48);*/black;
  --background-lv1: url("../picture-3.jpg");
  --background-lv2: url("../picture-2.jpg"); }

@keyframes sliden {
  from {
    opacity: 0;
    transform: translateY(-15rem); }
  to {
    opacity: 1;
    transform: translateY(0); } }

@keyframes slidenX {
  from {
    opacity: 0;
    transform: translateX(-15rem); }
  to {
    opacity: 1;
    transform: translateX(0); } }

@keyframes slidenXY {
  from {
    opacity: 0;
    transform: translateX(50rem); }
  to {
    opacity: 1;
    transform: translateX(0); } }

/*
*Witespace Reset*/
* {
  margin: 0;
  padding: 0; }

/*
*Box-sizing fix*/
html {
  box-sizing: border-box; }

*, *::after, *::before {
  box-sizing: inherit; }

html {
  font: var(--font-size-initial, 12px)/1.5 "Verdana", sans-serif; }

@media screen and (min-width: 500px) {
  html {
    font-size: var(--font-size-initial-lv2, 15px); } }

@media screen and (min-width: 700px) {
  html {
    font-size: var(--font-size-initial-lv3, 17px); } }

@media screen and (min-width: 1600px) {
  html {
    font-size: var(--font-size-initial-lv4, 19px); } }

body, table, th, td, form, input, textarea, option, select {
  font: inherit; }

h1, h2, h3, h4, h5, h6 {
  line-height: 1.2;
  padding-bottom: 0.5em; }

h1 {
  font-size: 2.441em; }

h2 {
