  margin-bottom: 1em;
  border-radius: 1em;
  font-size: 1.3rem;
  font-weight: 700;
  background: var(--background-lv2, url("../picture-2.jpg"));
  background-size: cover;
  border: 2px solid var(--color-text-header, #4bd61f);
  color: var(--color-text-header, #4bd61f); }
  button:hover, button:focus {
    background: var(--background-lv1, url("../picture-3.jpg"));
    background-size: cover;
    border: 2px solid var(--color-text-hoover, #2c4c30);
    color: var(--color-text-hoover, #2c4c30); }

